

class ClickerGame {


    constructor() {
        this.cat = 0;
        this.dog = 0;
        this.mouse = 0;
        this.clicks = 0;
        this.clicks_per_second = 0;
        this.money = 0;
    }

    click() {
        this.money++;
    }

    getMoney() {
        return this.money;
    }

    addCatClicker() {
        this.cat++;
    }

    addDogClicker() {
        this.dog++;
    }

    addMouseClicker() {
        this.mouse++;
    }
}

let game = new ClickerGame();

let a = 'hello world';
let money = document.getElementById('money');
money.addEventListener(
    "click", function(){
        game.click();
        this.innerHTML = 'Money: ' + game.getMoney();
    });


